.cart {    
    background-color: white;
    color: #573697;
} 
.menu-categorias{
    display: flex;
    align-content: center;
    align-items: center;
    justify-content: center;
    /*background-color: #6a65d8!important;*/
    background-color: #9453f7 !important;
    color: #ffffff!important;
}
.menu-principal {
    background-color: #573697 !important;
}
.menu-categorias a {
    color: #ffffff!important;
}
.menu-horizontal{
    position: -webkit-sticky!important;
    position: sticky!important;
    top: 0!important;
    width: 100%!important;
    height: auto;
    z-index: 9999!important;
}
.ui.menu{
    margin: 0!important;
    border-radius: 0!important;
}


/***** Configuracoa para menu mobile *****/


.ui.segment {
    margin: 0!important;
    border: none!important;
    border-radius: 0!important;
}

.pusher {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

.pusher > .footer {
    flex: 1 1;
}

.menu-mobile{
    display: none!important;
    background-color: #573697!important;
}

.dev-team{
    display: flex!important;
    flex-direction: row!important;
}

.btn-menu-mobile{
    display: none!important;
    position: absolute!important;
    z-index: 9999!important;
}

.icon-menu-mobile{
    background: transparent!important;
    /*color: #ffffff!important;*/
}
.sidebar{
    width: 200px!important;
}
.dev-team{
    display: flex!important;
    flex-direction: column!important;
}

@media only screen and (max-width: 800px) {
    .large-menu .item{
        display: none!important;
    }

    .menu-mobile{
        display: flex!important;
    }

    .btn-menu-mobile{
        display: flex!important;
    }

    .menu-categorias{
        justify-content: start;
    }

    .dev-team>.item {
        margin-right: 20px!important;
        width: 100px!important;
    }

}
footer {
    bottom: 0;
}

.footer {
    background-color: #573697 !important;
    padding: '5em 0em';
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1 {
    text-align: center;
}

.container-main{
    margin-top: 0vh;
}

main .ui.vertical.menu {
    float: left;
}

main .ui.grid>.column {
    width:190px !important;
    height:220px;
}

.infoProduto {
    text-align: center;
    font-size: 14px;
    font-weight: normal;
    margin: 0;
    overflow: hidden;
    line-height: 17px;
    width: 100%;
    white-space: normal;
    color: #666;
    text-transform: none;
}

.imagemProduto {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100px;
    height: 100px;
}

.precoProduto {
    font-weight: bold;
    font-size: 18px;
    color: #666;
    text-align: center;
    display: block;
    padding: 5px;
}

.pusher {
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}
  
.pusher > .footer {
    flex: 1 1;
}

.my-slider > .slick-list {
    padding: 1em;
}

.arrow-next {
    top: 50%;
    display: block;
    position: absolute;
    right: 25px;
}

.arrow-prev {
    top: 50%;
    display: block;
    position: absolute;
    left: -25px;
}
